$font_path: '../../fonts/';

@import '../../scss/helpers/helpers-all';
@import '../../scss/reset';

// ---------------------------------------------------------------------------------fonts#
@include font_face('RobotoCondensed-Regular', 'RobotoCondensed', $font_path, 400);
// ---------------------------------------------------------------------------------fonts##

// // ---------------------------------------------------------------------------------base#
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--gl-indent: 5rem;
	--content-width: 96rem;

	--font_1: 'RobotoCondensed', sans-serif;
	--font-size-html: calc(100vw / 1920 * 10);
	--font-size-body: 1.6rem;
	--color-text-base: #000;

	--body-background: #fff;
	--accent-color: #4aa3df;
	--input-placeholder-color: var(--color-text-base);
}

html {
	height: 100%;

	font-family: var(--font_1);
	font-weight: 400;
	line-height: 1.33;
	font-size: var(--font-size-html);
}

body {
	position: relative;

	height: 100%;

	font-size: var(--font-size-body);
	color: var(--color-text-base);

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: var(--body-background);
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;

	flex: 1 0 auto;
}

.section {
	position: relative;

	width: 100%;

	font-size: 1rem;

	//remove this demo style
	&.demo_mod {
		@include flex_block(row, nowrap, center, center, center);

		height: var(--full-screen);

		font-size: 4rem;
		color: #fff;
		text-align: center;

		background-color: #000;
	}
}

.section_in {
	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

a {
	text-decoration: none;
	color: var(--accent-color);

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}
// ---------------------------------------------------------------------------------base##
